<template>
  <div>
    <Header></Header>
    <div class="demo-input-suffix" style="margin-top:80px;">
      <el-row>
        <el-col :span="12">
          <div class="grid-content bg-purple-light">
            <el-button
              type="success"
              icon="el-icon-plus"
              round
              @click="addopen()"
              class="margin5"
            >添加</el-button>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="grid-content bg-purple-light">
            <el-button
              type="primary"
              icon="el-icon-refresh"
              circle
              @click="updataall"
              class="margin5"
            ></el-button>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="添加链接"
      :visible.sync="addopenDialog"
      @close="resetForm('addForms')"
      align="center"
      :before-close="handleClose"
    >
      <el-form :model="addForms" :rules="addRuless" ref="addForms" label-width="120px">
        <el-form-item label="链接Url" prop="linkurl">
          <el-input
            type="textarea"
            autosize
            placeholder="请输入小红书链接，注意格式！（一次性可以提交多个）"
            v-model="addForms.linkurl"
            class="allwidth"
          ></el-input>
        </el-form-item>
        <p :plain="true" @click="open1" style="cursor: pointer;margin-bottom:20px;">查看格式示例</p>
        <el-button type="primary" @click.stop="submitForms('addForms')">提交</el-button>
      </el-form>
    </el-dialog>
    <el-table
      :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)"
      border
      style="width: 100%;"
      v-loading="loading"
    >
      <el-table-column label="序号" prop="id" align="center"></el-table-column>
      <el-table-column label="链接Url" prop="noteLink" align="center">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>{{ scope.row.noteLink }}</p>
            <div slot="reference" class="name-wrapper">{{ scope.row.noteLink }}</div>
          </el-popover>
        </template>
      </el-table-column>

      <el-table-column label="爬取状态" prop="type" align="center">
        <template slot-scope="scope">
          <p v-if="scope.row.type==0" style="color:#F56C6C;">待爬取</p>
          <p v-if="scope.row.type==1" style="color:#67C23A;">已爬取</p>
          <p v-if="scope.row.type==2" style="color:#E6A23C;">正在爬取</p>
        </template>
      </el-table-column>
      <el-table-column label="添加时间" prop="createTime" align="center"></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="seemore(scope.row)">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="tabListPage">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="PageSize"
        layout="total, prev, pager, next, jumper"
        :total="totalCount"
      ></el-pagination>
    </div>
  </div>
</template>

