                "很多新手宝宝画眼妆，不知道怎么晕染，下手太重，导致眼皮上只有色块，眼影画的很脏😭😭\n👩🏻‍🦰我也是从新手小白一路过来的，总结了一些新手画眼影容易犯的错误👁掌握这些方法和熟练的手法相信你离美枚博主不远啦一起学起来📝\n1️⃣眼部打底\n眼枚第一步打底，直接上深颜色整个眼枚会很脏\n❌错误画法;选择的颜色太深，铺色范围太大\n第一步整个眼枚就脏兮兮了\n✔️正确画法:用眼影中颜色最浅的哑光色来铺满整个眼窝，要用刷子画，不要用手指涂抹\n2️⃣铺色\n❌错误画法;只加深双眼皮的褶皱处，这样过渡很不自然，界线太明显\n✔️正确画法:用比打底色深一点的颜色，来加深双眼皮褶皱，少量多次，晕染范围高于双眼皮的褶皱\n3️⃣加深眼尾\n❌错误画法:涂抹眼睛的整个后半段，范围超过上一步晕染色的位置，而且没有晕染开\n✔️正确画法:晕染眼尾后3/1处三角区，面积不要太大，耐心晕染开，不要有边界感\n4️⃣提亮\n❌错误画法:涂抹范围从眼头带眼中，亮片色取色过多，显得眼睛肿肿的。欧美枚的大范围提亮，并不适合日常妆容。\n✔️正确画法:取少量提亮色，点涂在眼皮中\n间，用手指或者化妆刷轻轻晕染开。可以给整个眼妆注入灵魂，增加层次感，让眼部更立体",
