                "['//ci.xiaohongshu.com/7ae59fd1-0da9-3cd7-9af4-2769ee247439?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/37eecf03-489e-378a-9c1f-c340781ca0d6?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/09010d37-9e8e-3e1f-a883-59afac83fe03?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/f804a7db-4821-3a5e-be77-17c8ee64143c?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/82a5802c-2b5f-3299-9f81-49db3c46d34a?imageView2/2/w/1080/format/jpg']",
