                "首先先忽略我视频里面下面的字幕[暗中观察R]拍视频的时候忘记关掉自动生成字幕了，hiahia\n🥑🥑🥑🥑🥑🥑🥑🥑🥑\n哈喽~小红薯的宝宝晚上好，今天给大家想要安利一款炒鸡好用的洁面仪！[黄金薯R][黄金薯R]\n我现在自己用过的有两款，一个是FOREO mini2，另一个就是我说的炒鸡好用的科莱丽Mia2辣！我自己的皮肤很喜欢长闭口，有时候还会莫名的冒一两颗痘，所以除了日常的好好护肤外，一周两到三次的深层清洁也是特别的重要～但是传统手洗总是感觉没那么干净，所以我还是需要一台洁面仪来帮助我做更深层的肌肤清洁\n入手型号：科莱丽Mia2粉色款\n产品配置：Mia2一台，充电器一个，洁面刷头1个\n主要功能：深层清洁肌肤，疏通毛孔，改善黑头，毛孔粗大\n2档转速：1档较轻柔，适合早上洗脸唤醒肌肤，2档清洁力较强，适合晚间卸妆深层清洁\n刷毛：杜邦无孔刷毛，超软不伤皮肤，说是在破壳鸡蛋上使用也无损伤，哈哈~\n使用方式：配合洗面奶或卸妆乳在脸部按区域打圈按摩\n清洁方式：水洗刷毛，因为刷毛的材质是杜邦无孔刷头，刷毛无孔无隙，细菌无法在刷头藏身，不会使肌肤二次污染\n我一般是晚上洗脸的时候去使用，一周2-3次，坚持了3个月下来，皮肤本身的粉刺闭口那些都会比以前少，因为每天都带妆上班，所以皮肤每次卸完妆都会比较暗沉，之前一直在用的FOREO是那种震动形式的，总感觉震个两分钟也没太大感觉，不知道是不是心理作用，总觉得刷毛的清洁力更强，总之就是很推荐的一款洁面仪，宝宝们有任何产品问题也可以留言我噢，一起变美吧~",
