              content:
                "🔵\n早上起床第一件事，就是好好洗个脸。\n🔵\n有段时间本人洗脸极其应付，用洗面奶在脸上随便搓不到半分钟就拿水冲掉，后果就是肉眼可见的闭口增多，想挤又不敢，不挤又对不起自己的强迫症，上妆还很不服贴，脸色奇怪的像戴了面具一样。\n🔵\n后来我就意识到了认真洗脸的重要性，今天来安利我用了快两年的洁面仪——ReFa CLEAR\n🔵\n价格：我在日本哪个商场买的忘记了，单机价格1780左右。\n🔵\n外观：白色流线型很好看，使用的时候刷头边有一圈蓝光，也很好看。机身设计防止了平放时刷头碰到桌面被污染的情况。\n🔵\n刷头：刷头用的是日本有名的熊野毛，巨软。使用这么久完全没有扎脸的感jio。刷毛形状是圆球形，刷头可微微伸缩，清洁鼻翼两侧、眼窝等部位更到位。\n🔵\n刷头建议半年更换一次，可以单独购买，300一个，所以洗完脸要冲干净再挤干刷头的水注意保养！我入手洗脸仪的时候顺便带了三个刷头，已经扔掉三个了，又要消费了，真好。\n🔵\n搭配洗面奶：入手时我买了柜姐推荐的配套洗面奶，但用完后并没有回购，感jio一般般，用自己的洗面奶就可以啦，建议搭配泡沫丰富的洗面奶使用。\n🔵\n模式：\n洗脸仪一共有三个模式，按电源键调节，三种模式都是一分钟自动停止，避免过度清洁反而伤害皮肤，我一般一半脸用一分钟，没什么特别的原因，大概是脸比较大吧…\nsoft模式：适合早上洗脸&amp;淡妆卸妆后使用\nclean模式：适合晚上洗脸&amp;浓妆卸妆后使用\ndeep模式：适合t区&amp;其他出油多、毛孔大的部位使用\n🔵\n使用感受：指望一个洁面仪解决皮肤问题是不可能的，但相比起其他热门洁面仪来说我最喜欢这个，洗完脸感觉很清爽，坚持使用闭口出现的频率降低了很多，总之除了没有配套的便携包旅行携带没那么方便之外都很ok！\n⚠️\n使用时要握住边缘的银色线，洗脸仪利用负离子微电流原理，跟使用者之间产生微电流使用效果更好。\n洗脸仪防水，充电座不防水，使用后擦干放回底座。\n不能跟卸妆产品一起用。",
