      // }
    },
    //分页
    // 每页显示的条数
    handleSizeChange(val) {},
    // 显示第几页
    handleCurrentChange(val) {
      if (this.routeid != "") {
        this.$axios
          .get(window.g.apiUrl + "/xhs/getNoteList", {
            params: {
              noteUrlRecommendId: this.routeid,
              contentType: null,
              twoClassify: this.product,
              minInteraction: this.num1,
              maxInteraction: this.num2,
              starttime: this.selecttime[0],
              endtime: this.selecttime[1],
              sort: this.typeway + " " + this.way,
              pageNum: val,
              pageSize: this.PageSize
            }
          })
          .then(res => {
            if (res.data.code == 200) {
              this.tableData = res.data.data.list;
              this.totalCount = res.data.data.total;
              this.$message.success("获取数据成功!");
            } else {
              this.$message.error(res.data.msg);
            }
          });
      } else {
        this.$axios
          .get(window.g.apiUrl + "/xhs/getNoteList", {
            params: {
              twoClassify: this.product,
              contentType: null,
              minInteraction: this.num1,
              maxInteraction: this.num2,
              starttime: this.selecttime[0],
              endtime: this.selecttime[1],
              sort: this.typeway + " " + this.way,
              pageNum: val,
              pageSize: this.PageSize
            }
          })
          .then(res => {
            if (res.data.code == 200) {
              this.tableData = res.data.data.list;
              this.totalCount = res.data.data.total;
              this.$message.success("获取数据成功!");
            } else {
              this.$message.error(res.data.msg);
            }
          });
      }
    },
    selectdata() {
      if (this.num1 >= this.num2 && this.num1 != "") {
        this.$message.error("互动量的最小量不能大于等于最大量!");
        return false;
      } else if (this.notes != "" && this.product == "") {
        this.$message.error("请选择产品类型!");
        return false;
      } else {
        this.getUserstableData();
      }
    },
    addopen() {
      this.$router.push({
        path: "/redbookform" //要跳转的页面路径
      });
    },
    callback() {
      this.$router.push({
        path: "/redbooktable" //要跳转的页面路径
      });
    }
  }
};
</script>
