<template>
  <div>
    <Header></Header>
    <div class="demo-input-suffix grid-content bg-purple-light" style="margin-top:80px;">
      <div class="selectheader">
        <template>
          标签:
          <el-autocomplete
            style="width:40%"
            v-model="labeltype"
            :fetch-suggestions="querySearchAsyncpingtai"
            placeholder="请输入标签:"
            clearable
            @focus="loadAllpingtai()"
          ></el-autocomplete>
        </template>
        <el-button type="info" icon="el-icon-search" round @click="selectdata()">搜索</el-button>
        <el-button type="primary" icon="el-icon-refresh" circle @click="updataall"></el-button>
      </div>
    </div>
    <div
      class="row clearfix"
      v-loading="loading"
      element-loading-text="数据正在拼命加载中....."
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.6)"
    >
      <div class="col-md-12 column">
        <div class="row clearfix">
          <div class="col-md-6 col-sm-12 col-xs-12 column">
            <vue-scroll>
              <div style="height:500px;">
                <div
                  id="myChart1"
                  :style="{width: '95%', height: '2000px'}"
                  style="margin:10px auto;display:block;"
                ></div>
              </div>
            </vue-scroll>
            <vue-scroll>
              <div style="height:500px;">
                <div
                  id="worldChart1"
                  :style="{width: '95%', height: '100%'}"
                  style="margin:10px auto;display:block;"
                ></div>
              </div>
            </vue-scroll>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 column">
            <vue-scroll>
              <div style="height:500px;">
                <div
                  id="myChart2"
                  :style="{width: '95%', height: '2000px'}"
                  style="margin:10px auto;display:block;"
                ></div>
              </div>
            </vue-scroll>
            <vue-scroll>
              <div style="height:500px;">
                <div
                  id="worldChart2"
                  :style="{width: '95%', height: '100%'}"
                  style="margin:10px auto;display:block;"
                ></div>
              </div>
            </vue-scroll>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "./../../public/Header";
export default {
  name: "UesrNoteskeyworld",
  inject: ["reload"],
  components: {
    Header
  },

  //组件渲染完毕后调用getUserstableData,来获取所有的用户列表
  mounted: function() {
    this.getUserstableData();
  },

  data() {
    return {
      loading: false,
      labeltype: "双肩包",
      restaurantspingtai: [],
      timeout: null, //公共

      xdatas1: [],
      ydatas1: [],
      max1: "",
      worldData1: [],
      xdatas2: [],
      ydatas2: [],
      max2: "",
      worldData2: []
    };
  },
  methods: {
    loadAllpingtai() {
      this.$axios.get(window.g.apiUrl + "/xhsUserNotes/getTags").then(res => {
        if (res.data.code == 200) {
          var data2 = [];
          res.data.data.map((value, index, arry) => {
            data2.push({
              value: value.name
            });
          });
          this.restaurantspingtai = data2;
        } else {
          this.$message.info(res.data.msg);
        }
      });
    },
    querySearchAsyncpingtai(queryString, cb) {
      var restaurantspingtai = this.restaurantspingtai;
      var results = queryString
        ? restaurantspingtai.filter(this.createStateFilter(queryString))
        : restaurantspingtai;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 1000 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    //柱状图
    drawLine(xins1, yins1, xins2, yins2, max1, max2) {
      // 基于准备好的dom，初始化echarts实例
      let myChart1 = this.$echarts.init(document.getElementById("myChart1"));
      // 绘制图表
      var colors = ["#5793f3"];

      myChart1.setOption({
        title: {
          text: "标题关键字",
          subtext: "柱状图"
        },
        color: colors,
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          }
        },
        grid: {
          width: "80%"
        },
        toolbox: {
          feature: {
            dataView: { show: true, readOnly: false },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        legend: {
          data: ["次数"]
        },
        xAxis: [
          {
            type: "value",
            name: "次数",
            min: 0,
            max: max1,
            position: "top",
            axisLine: {
              lineStyle: {
                color: colors[0]
              }
            },
            axisLabel: {
              formatter: "{value}次"
            }
          }
        ],
        yAxis: [
          {
            type: "category",
            axisLabel: {
              interval: 0,
              textStyle: {
                color: "#5793f3"
              }
            },
            axisTick: {
              alignWithLabel: true
            },
            data: yins1
          }
        ],
        series: [
          {
            name: "次数",
            type: "bar",
            barWidth: "20%",
            data: xins1
          }
        ]
      });
      // 基于准备好的dom，初始化echarts实例
      let myChart2 = this.$echarts.init(document.getElementById("myChart2"));
      // 绘制图表
      var colors = ["#5793f3"];

      myChart2.setOption({
        title: {
          text: "内容关键字",
          subtext: "柱状图"
        },
        color: colors,
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          }
        },
        grid: {
          width: "80%"
        },
        toolbox: {
          feature: {
            dataView: { show: true, readOnly: false },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        legend: {
          data: ["次数"]
        },
        xAxis: [
          {
            type: "value",
            name: "次数",
            min: 0,
            max: max2,
            position: "top",
            axisLine: {
              lineStyle: {
                color: colors[0]
              }
            },
            axisLabel: {
              formatter: "{value}次"
            }
          }
        ],
        yAxis: [
          {
            type: "category",
            axisLabel: {
              interval: 0,
              textStyle: {
                color: "#5793f3"
              }
            },
            axisTick: {
              alignWithLabel: true
            },
            data: yins2
          }
        ],
        series: [
          {
            name: "次数",
            type: "bar",
            barWidth: "10%",
            data: xins2
          }
        ]
      });
    },
    //词云图
    createRandomItemStyle() {
      return {
        normal: {
          color:
            "rgb(" +
            [
              Math.round(Math.random() * 160),
              Math.round(Math.random() * 160),
              Math.round(Math.random() * 160)
            ].join(",") +
            ")"
        }
      };
    },
    drawWorld(worldData1, worldData2) {
      // 基于准备好的dom，初始化echarts实例
      let worldChart1 = this.$echarts.init(
        document.getElementById("worldChart1")
      );

      worldChart1.setOption({
        title: {
          text: "标题词符云"
          // link: "http://www.google.com/trends/hottrends"
        },
        tooltip: {
          show: true
        },
        series: [
          {
            name: "标题词符云",
            type: "wordCloud",
            size: ["80%", "80%"],
            textRotation: [0, 45, 90, -45],
            textPadding: 0,
            autoSize: {
              enable: true,
              minSize: 14
            },
            data: worldData1
          }
        ]
      });
      // 基于准备好的dom，初始化echarts实例
      let worldChart2 = this.$echarts.init(
        document.getElementById("worldChart2")
      );

      worldChart2.setOption({
        title: {
          text: "内容词符云"
          // link: "http://www.google.com/trends/hottrends"
        },
        tooltip: {
          show: true
        },
        series: [
          {
            name: "内容词符云",
            type: "wordCloud",
            size: ["80%", "80%"],
            textRotation: [0, 45, 90, -45],
            textPadding: 0,
            autoSize: {
              enable: true,
              minSize: 14
            },
            data: worldData2
          }
        ]
      });
    },

    //初始化默认获取表格数据
