<template>
  <div>
    <Header></Header>
    <div class="demo-input-suffix" style="margin-top:80px;">
      <el-row>
        <el-col :span="8">
          <div class="grid-content bg-purple-light">
            <el-button
              type="success"
              icon="el-icon-plus"
              round
              @click="addopen()"
              class="margin5"
            >添加</el-button>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple-light">
            <el-button
              type="info"
              icon="el-icon-search"
              round
              @click="selecttabledata()"
              class="margin5"
            >总表</el-button>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple-light">
            <el-button
              type="primary"
              icon="el-icon-refresh"
              circle
              @click="updataall"
              class="margin5"
            ></el-button>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-table
      :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)"
      border
      style="width: 100%;"
      v-loading="loading"
      :row-class-name="tableRowClassName"
    >
      <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
      <el-table-column label="博主ID" prop="wid" align="center"></el-table-column>
      <el-table-column label="博主昵称" prop="screenName" align="center"></el-table-column>
      <el-table-column label="开始时间" prop="startTime" align="center"></el-table-column>
      <el-table-column label="结束时间" prop="endTime" align="center"></el-table-column>
      <el-table-column label="内容类型" prop="crawlerType" align="center">
        <template slot-scope="scope">
          <p v-if="scope.row.crawlerType==1" @click="turnRouter(scope.row)" class="turnrouter">全部内容</p>
          <p v-if="scope.row.crawlerType==2" @click="turnRouter(scope.row)" class="turnrouter">广告内容</p>
          <p v-if="scope.row.crawlerType==3" @click="turnRouter(scope.row)" class="turnrouter">非广告内容</p>
        </template>
      </el-table-column>
      <el-table-column label="爬取状态" prop="crawlerState" align="center">
        <template slot-scope="scope">
          <p v-if="scope.row.crawlerState==0" style="color:#F56C6C;">待爬取</p>
          <p v-if="scope.row.crawlerState==1" style="color:#67C23A;">已爬取</p>
          <p v-if="scope.row.crawlerState==2" style="color:#E6A23C;">正在爬取</p>
        </template>
      </el-table-column>
      <el-table-column label="添加时间" prop="createTime" align="center"></el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="tabListPage">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="PageSize"
        layout="total, prev, pager, next, jumper"
        :total="totalCount"
      ></el-pagination>
    </div>
  </div>
</template>

