    selecttabledata() {
      this.$router.push({
        path: "formtablemore" //要跳转的页面路径
      });
    },
    addopen() {
      this.$router.push({
        path: "/form" //要跳转的页面路径
      });
    },
    turnRouter(ins) {
      if (ins.crawlerType == 1) {
        this.$router.push({
          path: "formtableall", //要跳转的页面路径
          query: {
            id: ins.id, //传递的参数，方法要接收ins
            name: ins.screenName,
            crawlerType: "全部内容"
          }
        });
      } else if (ins.crawlerType == 2) {
        this.$router.push({
          path: "formtableyes", //要跳转的页面路径
          query: {
            id: ins.id, //传递的参数，方法要接收ins
            name: ins.screenName,
            crawlerType: "广告内容"
          }
        });
      } else if (ins.crawlerType == 3) {
