<template>
  <div id="RedBookshow">
    <Header></Header>
    <el-container style="margin-top:80px;">
      <el-main>
        <div class="paqupage" style="margin-top:20px;">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" status-icon>
            <el-form-item label="爬取链接" prop="paquurl">
              <el-input
                placeholder="请输入爬取链接"
                v-model="ruleForm.paquurl"
                clearable
                style="width:90%;"
              ></el-input>
            </el-form-item>
            <el-form-item label="笔记类型" prop="notes">
              <el-autocomplete
                v-model="ruleForm.notes"
                :fetch-suggestions="querySearchAsyncnotes"
                placeholder="请输入笔记类型:"
                clearable
                style="width:90%;"
                @focus="loadAllnotes()"
              ></el-autocomplete>
            </el-form-item>
            <el-form-item label="产品类型" prop="product">
              <el-autocomplete
                v-model="ruleForm.product"
                :fetch-suggestions="querySearchAsyncproduct"
                placeholder="请输入产品类型:"
                clearable
                style="width:90%;"
                @focus="loadAllproduct()"
              ></el-autocomplete>
            </el-form-item>
            <el-form-item label="爬取数量" prop="num">
              <el-input
                v-model.number="ruleForm.num"
                autocomplete="off"
                clearable
                style="width:90%;"
              ></el-input>
            </el-form-item>
            <div class="container">
              <div class="row clearfix">
                <div class="col-md-12 column">
                  <el-button type="primary" @click="submitForm('ruleForm')" :loading="logining">立即爬取</el-button>
                  <el-button @click="resetForm('ruleForm')" :loading="logining">重置</el-button>
                  <el-button type="primary" @click="openeditDialog()">编辑</el-button>
                </div>
              </div>
            </div>
          </el-form>
        </div>
        <el-dialog
          title="编辑类型"
          :visible.sync="editDialogVisible"
          width="90%"
          center
          @close="close('editform')"
        >
          <el-form
            :model="editform"
            :rules="editrules"
            ref="editform"
            style="text-align:center;"
            status-icon
            label-width="120px"
          >
            <el-form-item label="选择类型" prop="value">
              <el-select
                v-model="value"
                clearable
                placeholder="请选择编辑类型"
                style="width:100%;"
                @change="selecttype('editform')"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="笔记类型" prop="editnotesid" v-if="this.value==1">
              <el-select
                v-model="editform.editnotesid"
                clearable
                placeholder="请选择需要编辑的笔记类型"
                style="width:100%;"
                @change="getAllproduct()"
              >
                <el-option
                  v-for="item in getnotes"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="笔记类型" prop="editnotesid" v-if="this.value==2">
              <el-select
                v-model="editform.editnotesid"
                clearable
                placeholder="请选择需要编辑的笔记类型"
                style="width:100%;"
                @change="getAllproduct()"
              >
                <el-option
                  v-for="item in getnotes"
                  :key="item.name"
                  :label="item.name"
                  :value="item.name"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="产品类型" prop="editproductid" v-if="this.value==2">
              <el-select
                v-model="editform.editproductid"
                clearable
                placeholder="请选择需要编辑的产品类型"
                style="width:100%;"
              >
                <el-option
                  v-for="item in getproduct"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="更新名" prop="newname">
              <el-input v-model="editform.newname" placeholder="请填写更新名" clearable></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="addeditform('editform')">提交</el-button>
          </span>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
</template>
<script>
import Header from "./../../public/Header";

export default {
  name: "RedBookshow",
  inject: ["reload"],
  components: {
    Header
  },
  data() {
    return {
      //爬取页面数据
      logining: false,
      editDialogVisible: false,
      getnotes: [],
      getproduct: [],
      editform: {
        editnotesid: "",
        editproductid: "",
        newname: ""
      },
      value: "",
      options: [
        {
          value: 1,
          label: "笔记类型"
        },
        {
          value: 2,
          label: "产品类型"
        }
      ],
      editrules: {
        editnotesid: [
          {
            required: true,
            message: "请选择需要编辑的笔记类型",
            trigger: "change"
          }
        ],
        newname: [
          { required: true, message: "更新名不能为空", trigger: "blur" }
        ],
        editproductid: [
          {
            required: true,
            message: "请选择需要编辑的产品类型",
            trigger: "change"
          }
        ]
      },

      //input远程搜索
      restaurantsnotes: [],
      timeout: null, //公共
      restaurantsproduct: [],

      ruleForm: {
        paquurl: "",
        notes: "",
        product: "",
        num: ""
      },

      rules: {
        paquurl: [
          { required: true, message: "爬取链接不能为空", trigger: "blur" },
          {
            pattern: /(http|https):\/\/www.xiaohongshu.com/,
            message: "填写正确的小红书地址",
            trigger: "blur"
          }
        ],
        notes: [
          { required: true, message: "笔记类型不能为空", trigger: "blur" }
        ],
        product: [
          { required: true, message: "产品类型不能为空", trigger: "blur" }
        ],
        num: [
          { required: true, message: "数量不能为空", trigger: "blur" },
          { type: "number", message: "数量必须为数字值", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {},

  methods: {
    selecttype(formName) {
      this.$refs[formName].resetFields();
      this.getAllnotes();
      if (this.value == 2) {
        this.getAllproduct();
      }
    },
    getAllnotes() {
      this.$axios.get(window.g.apiUrl + "/xhs/getOneClassify").then(res => {
        if (res.data.code == 200) {
          this.getnotes = res.data.data;
        } else {
          this.$message.info(res.data.msg);
        }
      });
    },
    close(formName) {
      this.$refs[formName].resetFields();
    },
    getAllproduct() {
      this.$axios
        .get(window.g.apiUrl + "/xhs/getTwoClassify", {
          params: { name: this.editform.editnotesid }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.getproduct = res.data.data;
          } else {
            this.$message.info(res.data.msg);
          }
        });
    },

    addeditform: function(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.logining = true;
          const url = window.g.apiUrl + "/xhs/updateClassifyName";
          var params = new URLSearchParams();
          if (this.value == 1) {
            params.append("id", this.editform.editnotesid);
            params.append("name", this.editform.newname);
          } else if (this.value == 2) {
            params.append("id", this.editform.editproductid);
            params.append("name", this.editform.newname);
          }

          this.$axios({
            method: "post",
            url: url,
            data: params
          }).then(res => {
            if (res.data.code == 200) {
              this.$message.success(res.data.msg);
              this.updataall();
            } else {
              this.$message.error(res.data.msg);
            }
          });
        } else {
          this.$message.error("填写完整表单!");
          return false;
        }
      });
    },
    //笔记类型搜索输入框
    loadAllnotes() {
      this.$axios.get(window.g.apiUrl + "/xhs/getOneClassify").then(res => {
        if (res.data.code == 200) {
          var data2 = [];
          res.data.data.map((value, index, arry) => {
            data2.push({
              id: value.id,
              value: value.name
            });
          });
          this.restaurantsnotes = data2;
        } else {
          this.$message.info(res.data.msg);
        }
      });
    },
    querySearchAsyncnotes(queryString, cb) {
      var restaurantsnotes = this.restaurantsnotes;
      var results = queryString
        ? restaurantsnotes.filter(this.createStateFilter(queryString))
        : restaurantsnotes;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 1000 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    //产品类型输入框
    loadAllproduct() {
      this.restaurantsproduct = [];
      this.$axios
        .get(window.g.apiUrl + "/xhs/getTwoClassify", {
          params: { name: this.ruleForm.notes }
        })
        .then(res => {
          if (res.data.code == 200) {
            if (res.data.data != null) {
              var data2 = [];
              res.data.data.map((value, index, arry) => {
                data2.push({
                  id: value.id,
                  value: value.name
                });
              });
              this.restaurantsproduct = data2;
            }
          } else {
            this.$message.info(res.data.msg);
          }
        });
    },
    querySearchAsyncproduct(queryString, cb) {
      var restaurantsproduct = this.restaurantsproduct;
      var results = queryString
        ? restaurantsproduct.filter(this.createStateFilter(queryString))
        : restaurantsproduct;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 1000 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    openeditDialog() {
      this.editDialogVisible = true;
    },

    //爬取提交
    submitForm(formName) {
      if (this.ruleForm.notes == this.ruleForm.product) {
        this.$message.error("笔记类型与产品类型同名了!");
        return false;
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.logining = true;
          const url = window.g.apiUrl + "/xhs/add";
          var params = new URLSearchParams();
          params.append("url", this.ruleForm.paquurl);
          params.append("noteTypeName", this.ruleForm.notes);
          params.append("productTypeName", this.ruleForm.product);
          params.append("num", this.ruleForm.num);
          this.$axios({
            method: "post",
            url: url,
            data: params
          }).then(res => {
            if (res.data.code == 200) {
              this.logining = false;
              this.$message.success("爬取成功!");
              this.clearform();
            } else {
              this.$message.error(res.data.msg);
            }
          });
        } else {
          this.$message.error("填写完整表单!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.updataall();
    },
    //上传完清空表单
    clearform() {
      this.$refs["ruleForm"].resetFields();
      this.updataall(); //刷新当前页
    },
    //刷新页面
    updataall() {
      this.reload(); //刷新当前页
    }
  }
};
</script>
<style scoped>
.el-main {
  line-height: 0px !important;
  padding: 20px 0px !important;
  text-align: center;
}
</style>



