<template>
  <div>
    <Header></Header>
    <div style="width:90%;margin:0px auto;  text-align: center;margin-top: 120px;">
      <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="120px" status-icon>
        <el-form-item label="博主链接" prop="url">
          <div>
            <vue-tags-input
              v-model="addForm.url"
              :tags="tags"
              @tags-changed="newTags => tags = newTags"
              placeholder="请填写博主链接"
            />
          </div>
        </el-form-item>
        <el-form-item label="抓取时间" prop="time1">
          <el-date-picker
            v-model="addForm.time1"
            type="date"
            placeholder="开始时间"
            value-format="yyyy-MM-dd"
            style="width: 200px ;"
            class="allwidth"
            auto-complete="off"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="抓取时间" prop="time2">
          <el-date-picker
            v-model="addForm.time2"
            type="date"
            placeholder="结束时间"
            value-format="yyyy-MM-dd"
            class="allwidth"
            style="width: 200px ;"
            auto-complete="off"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="抓取内容" prop="content">
          <el-select
            v-model="addForm.content"
            clearable
            placeholder="请选择抓取内容"
            auto-complete="off"
            class="allwidth"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="收取邮箱" prop="email">
          <el-input
            type="text"
            v-model="addForm.email"
            auto-complete="off"
            clearable
            class="allwidth"
          ></el-input>
        </el-form-item>
        <el-button type="primary" @click.stop="submitForm('addForm')">提交</el-button>
        <el-button type="info" @click="resetForm()">重置</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
import VueTagsInput from "@johmun/vue-tags-input";
import Header from "../public/Header";
export default {
  name: "Form",
  inject: ["reload"], //引入方法
  components: {
    VueTagsInput,
    Header
  },
  data() {
    var checkaurl = (rule, value, callback) => {
      if (this.ass == 1) {
        callback(new Error("Url不能为空!,输完每条url请按Enter/return键!"));
      }
      callback();
    };
    return {
      tags: [],
      options: [
        {
          value: 1,
          label: "全部内容"
        },
        {
          value: 2,
          label: "广告内容"
        },
        {
          value: 3,
          label: "非广告内容"
        }
      ],
      addForm: {
        url: "",
        time1: "",
        time2: "",
        content: "",
        email: ""
      },
      ass: "",
      addRules: {
        url: [{ validator: checkaurl, trigger: "blur" }],
        time1: [
          { required: true, message: "爬取开始时间不能为空", trigger: "change" }
        ],
        time2: [
          { required: true, message: "爬取结束时间不能为空", trigger: "change" }
        ],
        content: [
          { required: true, message: "爬取内容不能为空", trigger: "change" }
        ]
        // email: [
        //   { required: true, message: "收取邮箱不能为空", trigger: "blur" },
        //   {
        //     pattern: /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
        //     message: "请正确填写邮箱",
        //     trigger: "blur"
        //   }
        // ]
      }
    };
  },
  methods: {
    submitForm: function(formName) {
      if (this.tags.length == 0) {
        this.ass = 1;
      } else {
        this.ass = "";
      }
      var arr = [];
      for (var i = 0; i < this.tags.length; i++) {
        arr.push(this.tags[i].text);
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.addForm.time1 > this.addForm.time2) {
            this.$message.error("开始时间不能大于结束时间!");
            return false;
          } else {
            const url = window.g.apiUrl + "/weibo/add";
            var params = new URLSearchParams();
            params.append("url", JSON.stringify(arr));
            //params.append("url", this.addForm.url);
            params.append("starttime", this.addForm.time1);
            params.append("endtime", this.addForm.time2);
            params.append("crawlerType", this.addForm.content);
            params.append("email", this.addForm.email);
            this.$axios({
              method: "post",
              url: url,
              data: params
            }).then(res => {
              if (res.data.code == 200) {
                this.$message.success("添加成功!");
              } else {
                this.$message.info(res.data.msg);
              }
              this.updataall();
            });
          }
        } else {
          return false;
        }
      });
    },
    updataall() {
      this.reload(); //刷新当前页
    },
    resetForm() {
      this.$refs["addForm"].resetFields();
    }
  }
};
</script>

<style scoped>
.allwidth {
  width: 200px;
}
</style>


