<template>
  <div>
    <div class="container">
      <div class="row clearfix">
        <div class="col-md-12 column">
          <nav class="navbar navbar-default navbar-fixed-top" role="navigation">
            <div class="navbar-header">
              <button
                type="button"
                class="navbar-toggle"
                data-toggle="collapse"
                data-target="#bs-example-navbar-collapse-1"
              >
                <span class="sr-only">Toggle navigation</span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
              </button>
              <img class="navbar-brand logo" :src="logoimg" alt="logo">
              <div class="navbar-brand logo_title">
                <a @click="navchange(1)">爬取项目</a>
              </div>
            </div>

            <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
              <ul class="nav navbar-nav navbar-right">
                <li class="dropdown" v-for="(item,index) in navbarlist" :key="index">
                  <a
                    class="alink"
                    data-toggle="dropdown"
                    :class="{active:navchangeIndex == index+1}"
                    @click="navchange(item.id,item.url)"
                  >
                    {{item.title1}}
                    <strong class="caret" v-if="item.childlist!=''"></strong>
                  </a>
                  <ul class="dropdown-menu" v-if="item.childlist!=''">
                    <li v-for="(items,index) in item.childlist" :key="index">
                      <a @click="childnavchange(item.id,items.id,items.url)">{{items.title2}}</a>
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      navchangeIndex: 1,
      logoimg: "./static/image/logo.png",
      navbarlist: [
        {
          id: 1,
          title1: "微博",
          childlist: [
            { id: 11, title2: "微博爬取", url: "/" },
            { id: 12, title2: "微博广告", url: "/weiboguanggao" }
          ],
          url: ""
        },
        {
          id: 2,
          title1: "小红书内容库",
          childlist: [
            { id: 21, title2: "关联笔记", url: "/redbooktable" },
            { id: 22, title2: "标签笔记", url: "/uesrnoteskeyworld" },
            { id: 23, title2: "链接笔记", url: "/linknotesview" }
          ],
          url: ""
        }
      ]
    };
  },
  mounted() {
    this.getnavnum();
  },
  methods: {
    getnavnum() {
      if (sessionStorage.getItem("navnum") != null) {
        this.navchangeIndex = sessionStorage.getItem("navnum");
      } else {
        sessionStorage.setItem("navnum", this.navchangeIndex);
      }
    },
    setnavnum(index) {
      sessionStorage.setItem("navnum", index);
    },
    navchange(index, url) {
      this.setnavnum(index);
      if (index == 1) {
        this.$router.push(url);
      }
    },
    childnavchange(fid, cid, url) {
      this.setnavnum(fid);
      this.$router.push(url);
      // if (cid == 21) {
      //   this.$router.push(url);
      // }
      // if (cid == 22) {
      //   this.$router.push(url);
      // }
      // if (cid == 23) {
      //   this.$router.push(url);
      // }
    }
  }
};
</script>
<style scoped>
.navbar-default {
  background-color: #409eff;
}
.navbar-brand {
  height: 80px;
  line-height: 45px;
}
.navbar-nav > li > a {
  padding-top: 30px;
}
.navbar-toggle {
  margin-top: 20px;
}
.navbar-right {
  margin-right: 50px !important;
}
.logo {
  padding: 8px;
}
.logo_title a {
  font-size: 30px;
}
a {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.active {
  color: #fff !important;
}

.navbar-default .navbar-nav > li > a {
  color: #303133de;
  font-size: 20px;
  line-height: 35px;
}
.dropdown-menu {
  background-color: #409eff;
}
.dropdown-menu > li > a {
  color: #fff;
  font-size: 18px;
}
.dropdown-menu > li:hover > a {
  color: #409eff;
}
</style>



