                "我有一侧乳头凹陷，怕bb出生后吸不住，孕后期就开始看各类电动吸奶器的测评以及店铺内购买后的评价，最后选了这款。（购买的时候客服会根据你乳头跟乳晕的直径来推荐合适的乳罩，这点很重要！不合适的乳罩会加重乳头拉扯，我一开始买的电动吸奶器就把乳头吸秃噜皮了，巨疼！大家一定要挑选合适的乳罩）\n我使用的方法是先把乳头罩对准乳头，让乳头保持在乳罩中间，然后开机，使用按摩模式01（一开始会有一点疼奶水出来之后感觉就像宝宝吸吮），然后调整按摩模式的强度（一共5档，根据自己承受能力调整），出奶阵之后我会切换到泌乳模式32 05，这个是比较适合我的，奶阵结束之后再切换到按摩模式，如此反复。\n👼👼👼（分割线）👼👼👼\n我是先租用后购买的，月子第五天到手，回来给奶瓶以及吸奶部件进行消毒后就试了下，第一次吸出来30ml，不过不够宝宝吃。之后就一直没用在家放着（感觉孩子的胃被婆婆灌大了，我婆婆属于传统观念非常强的一个人，宝宝只要一哭，就让喂奶，我奶出不来了，就要灌奶粉）\n👼🏻👼🏻👼🏻（分割线）👼🏻👼🏻👼🏻\n快出月子的时候，有一晚我着凉发烧了（不是乳腺炎），怕传染给宝宝就晚上单独睡，夜里奶胀，就拿出这个吸奶器吸奶，第一次是晚上一点半吸的，两边吸了120ml（我的🐴呀），当时惊呆了，我竟然还有这么多奶，不过体温38.2，没敢给宝宝喝就倒了。\n第二次是晚上三点半吸的，两边吸了130ml，吸完之后量了下体温37.4，于是就留下来放冰箱，第二天早上给宝宝喝了。\n这之后我信心爆棚，觉得自己母乳完全能喂饱宝宝，虽然我婆婆还是天天说孩子吃不饱，说我奶不够要加奶粉，我态度很坚决不让加，宝宝饿了就多吸奶，之后奶就越来越多了，但是奶量很不稳定，就在小红书上搜了很多追奶帖，开始试用pp追奶法，今天是pp追奶的第五天，配合五红汤当水喝，总体奶量比之前增加了20ml，吸夜奶真的很辛苦，不过没回亲喂的之后看到宝宝一脸满足的小表情真的很开心～",
