                "首先交代下 本人应该是中性肤质 受季节影响 夏天混油 冬天偏干 所以会根据季节和皮肤状态选择不同的洁面  下面就来说说我总结的洗脸那些事～[斜眼R]\n🎀THE GINZA洁面乳🎀\n贵妇级皂基洁面  泡沫丰富细腻 看配方成分都很温和 使用感不错 干净不紧绷  作为晚间洁面在使用\n毕竟价格比较贵 会不会回购看心情[吃瓜R]\n推荐指数💜💜💜\n🎀CPB清爽型洁面🎀\n清爽型仍然是一款皂基洁面  膏体有小颗粒 泡沫丰富绵密 洗完干净清爽  作为晚间洁面在使用\n价格适中 清洁力和使用感已经让我用到第二个了\n推荐指数💜💜💜💜\n🎀SUQQU高保湿洁面乳🎀\n是几款洁面中少有不含防腐剂的 就是味道不太高级 淡淡的肥皂味 不难闻 我也不享受[笑哭R] 泡沫多 感觉没有前两款保湿 但也不至于到紧绷的地步 作用晚间洁面在使用\n算是性价比还行的了\n推荐指数💜💜💜\n🎀黛珂AQ珍萃精颜洁面露🎀\n透明液体 价格高但是每次半泵到一泵 能用很久 氨基酸洁面 成分没有上三款皂基安全 泡沫属于细腻不厚实 洗完感觉很滋润保湿 滑滑的  夏天作为晨间洁面 晚间偶尔使用  但是春秋早晨洗完鼻子仍然起皮 虽说用到第二罐了 但今天查了成分表让我考虑可能不会回购了\n推荐指数💜💜💜\n🎀京都蒟蒻手工皂🎀\n纯粹是好玩买的  主打纯天然 无添加 DuangDuang的好可爱 黄金的这款颜值很高有没有  配合起泡网 泡沫异常丰富厚实是我喜欢的类型 平时没在意  想起来用一下  直到发现同样的季节早晨 使用上款黛珂鼻子起皮而这款不会 清洁力保湿力也够 立马对它另眼相看  具体价格不记得了 在京都买的 100左右？必须回购啊\n推荐指数💜💜💜💜💜\n🎀丝塔芙温和洁面乳🎀\n干手干脸使用的无泡乳液状洁面  在秋冬的早晨还是想用洗面奶的就选这款  清洁力不强 但也不会感觉洗不干净  用完挺舒服的 但是主打的温和不刺激的 查了下成分倒并不是完全都安全[笑哭R]反正我冬天早晨一直用它 家里常备款 便宜大碗\n推荐指数💜💜💜💜\n最后说几句 我是一年四季早晚都用洁面的人 选不同洁面而已 晚上还配合洁面刷 这样已经好多年了 皮肤状态一直很好 对于我来说不存在过度清洁 年轻时候黑头很多 现在晚上配合suqqu毛孔洁净精华 定期清洁面膜 那种硬硬的小黑头已经几乎没有了 所以 找到适合自己皮肤的清洁方法才是最重要的   希望对各位小红薯们有帮助[飞吻R][飞吻R][飞吻R]",
