                "1⃣眼部打底\n眼妆第一步打底， 直接上深颜色整个眼妆会很脏\n✅正确画法:用眼影中颜色最浅的哑光色来铺满整个眼窝，要用刷子画，不要用手指涂抹\n2⃣铺色\n✅正确画法:用比打底色深一点的颜色，来加深双眼皮褶皱，少量多次，晕染范围高于双眼皮的褶皱\n3⃣加深眼尾\n✅正确画法:晕染眼尾后3/1处三角区，面积不要太大，耐心晕染开，不要有边界感\n4⃣提亮\n✅正确画法:蘸取少量提亮色，点涂在眼皮中间，用手指或者化妆刷轻轻晕染开。可以给整个眼妆注入灵魂，增加层次感，让眼部更立体\nok就这么简单！！不会化的试试吧",
