              content:
                "2、便携性\n背奶妈妈每天背着沉重的装备奔波，非常辛苦。为了减轻负担，需要一件便携式的吸奶器。\n评测方式：主机称重\n◆小结：吸奶器主机还没一个苹果重，超级轻巧，放在包里很轻盈~妈妈办公、旅行可随身携带。\n便携程度：★★★★★\n \n3、供电方式\n使用电动吸奶器，最怕关键时刻没电。那么这款吸奶器的供电方式是什么样的？是否方便日常使用呢？\n评测方式：实操\n◆小结：吸奶器内置聚合物锂电池，续航能力持久，支持无线吸乳5次，外出背奶不怕没电尴尬。USB接口，跟手机充电一样方便，充电宝也能应急。\n供电便利性：★★★★★\n \n4、噪音测试\n吸奶器的噪音不能太大，否则会影响同事工作，在家影响家人及宝宝休息。为了让妈妈用的更舒心，测试一下噪音。\n评测方式：1.用人耳感受吸乳过程中的声音；2.用分贝仪器测试吸乳过程中的声音。\n◆小结：吸奶器处在工作状态下，感觉不刺耳。用分贝仪器测试，声音为40分贝左右，是人耳交流的正常声音，完全不用担心影响到别人。\n安静系数：★★★★★\n \n5、吸力评测\n每位妈妈体质不同，对力度的感知也不一样，一款好的吸奶器应是可调节的，能让妈妈找到适合自己的档位。那这款吸奶器的吸力怎样呢？通过小实验来证明一下！\n评测方式：用气球模拟乳房，观察吸力\n◆小结：用装水的气球模拟妈妈乳房，观察其吸力情况。1档是按摩催乳模式，气球没有明显凸起。2档气球轻微被吸起，可以很轻柔的吸乳。3、4档气球明显有节奏地被吸出，能够快速吸出母乳。连气球这么脆弱的东西在最高档位都没破裂，妈妈吸乳过程中自然不会感觉痛感，超级nice~\n吸力大小：★★★★★\n \n6、适配性\n对于奶量多的妈妈来讲，一个小奶瓶根本不够用，如果吸奶器可以和别的奶瓶适配，就无需买配件。那这款吸奶器是否能与别的品牌的奶瓶安装呢？\n评测方式：拿其他品牌奶瓶安装，看是否适配\n◆小结：将吸奶器和其他品牌的奶瓶安装，发现可以安装上，不需要再去买专用的奶瓶，非常经济实用；除此之外母乳储存也至关重要，这款吸奶器会赠送储奶袋和转接口，可以直接连接储奶袋轻松搞定吸奶、存乳、喂奶的全过程。\n适配性：★★★★★\n \n评测总结\n综上所述，这款便携式吸奶器小巧轻盈，方便妈妈外出办公、旅行携带。吸奶器的配件数量少，材质安全环保，方便妈妈清洗安装。4档可调节，妈妈可以根据需求找到适合自己的档位，没有多余按键，操作简单方便。\n推荐指数：★★★★★",
              pics:
                "['//ci.xiaohongshu.com/d90ad316-35ac-37ef-ae01-210297350166?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/e9cdd008-b6d3-30b8-8c49-b91d8430e96e?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/eb753724-ca0d-3687-8771-6222922e6bc1?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/fad56c8c-2a45-3a54-8143-08c22bd16da8?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/f6269fac-e123-386e-9001-7d844748afd5?imageView2/2/w/1080/format/jpg', '//ci.xiaohongshu.com/8cb19630-9dcf-33b5-a0d5-7e46cd1502c0?imageView2/2/w/1080/format/jpg']",
