              createTime: "2019-07-23 19:02:10.0",
              content:
                "夏天到了之后，我最大的感觉，就是脸上出油变多，洗脸也会比以前频繁一些，所以今天，就想跟大家聊一聊洗脸仪。\n自从科莱丽和Luna横空出世之后，估计很多妹纸都有疑惑——我到底买哪种才好，毕竟价格都有点小贵，认真挑选是大事！\n \n今天，我用洁面仪界的扛把子给大家做一下小测评！\n \n本次评测的是一款Luna，一款科莱丽Mi，下面就来跟大家详聊~\n \n \nLuna play plus\n参考价：¥399\n \n性价比：★★★★★\n起泡能力：★★★★\n清洁力度：★★★\n \nplay plus洗脸仪是luna里便宜的一个， paly plus作为之前的一次性洗脸仪Luna play的升级版，最大的特点，就是它不再是一次性的了。虽然这款不能充电，但是这款paly plus的底部可以更换电池，性价比一下子就高了起来。\n虽然说play plus的清洁模式只有一档，但是play plus的起泡能力很好，打出来也是非常细密的小泡沫，泡沫不是很丰富，都是一些贴合皮肤的小泡沫，所以摸上去会感觉比较柔软绵密。\n \n不过清洁能力感觉有点弱，15秒，眉笔和粉底液祛除的炒鸡干净，但是两支口红的印子却很明显。\n \n优点：不需要更换刷头，便携小巧，换一次电池能用很久\n缺点：清洁能力相对较弱，功能比较单一\n \n科莱丽 Mia2\n参考价：¥1299\n 性价比：★★★★\n起泡能力：★★★☆\n清洁力度：★★★★☆\n \nMia2应该是科莱丽性价比最高的一款，相比起Mia只有一个档位的清洁强度，Mia2一共有两个档位可以选择（低速+普通），操作起来还是挺简单的。\n Mia2的大小属于我觉得合适的那种，和6S差不多大，握着比较舒服，而且外出携带也很方便。充电一次可以使用24分钟，大概三个星期的样子\n刷头更换的话，两个月左右换一次，mia2除了清洁刷头外，还可以使用美妆刷头，等于换个刷头，多了一个底妆仪。\n \n科莱丽的起泡速度有些慢，同样的时间，洗面奶泡沫才刚刚出现，气泡比较大，比较稀疏的那种。\n科莱丽虽然起泡能力一般，但是清洁效果非常强，手背上只剩下哑光唇膏的一点点痕迹，其它的彩妆全部都清洁掉了。\n \n科莱丽的刷头选择很多，上脸之后的触感比较柔软，是我觉得科莱丽最大的优点，并且可以一机多用，换个刷头，就能成为美妆工具。\n说实话，买了洗脸仪之后，我最大的感觉就是——以前的脸都白洗了，仿佛之前自己根本就没有在洗脸！",
              pics:
