    handleCurrentChange(val) {
      if (this.routeid != "") {
        this.$axios
          .get(window.g.apiUrl + "/xhs/getNoteList", {
            params: {
              noteUrlRecommendId: this.routeid,
              contentType: "normal",
              twoClassify: this.product,
              minInteraction: this.num1,
              maxInteraction: this.num2,
              starttime: this.selecttime[0],
              endtime: this.selecttime[1],
              sort: this.typeway + " " + this.way,
              pageNum: val,
              pageSize: this.PageSize
            }
          })
          .then(res => {
            if (res.data.code == 200) {
              this.tableData = res.data.data.list;
              this.totalCount = res.data.data.total;
              this.$message.success("获取数据成功!");
            } else {
              this.$message.error(res.data.msg);
            }
          });
      } else {
