import Vue from 'vue'
import Router from 'vue-router'
import Form from '@/components/paquform/Form';
import Formtable from '@/components/paquform/Formtable';
import Formtableall from '@/components/paquform/Formtableall';
import Formtablemore from '@/components/paquform/Formtablemore';
import Formtableno from '@/components/paquform/Formtableno';
import Formtableyes from '@/components/paquform/Formtableyes';
import WeiboGuanggao from '@/components/paquform/WeiboGuanggao';
import RedBooktable from '@/components/redbookfactory/notesrecommend/RedBooktable';
import RedBookview from '@/components/redbookfactory/notesrecommend/RedBookview';
import RedBookform from '@/components/redbookfactory/notesrecommend/RedBookform';
import UesrNoteskeyworld from '@/components/redbookfactory/usernotes/UesrNoteskeyworld';
import Linknotesview from '@/components/redbookfactory/linknotes/Linknotesview';
import Linknotestable from '@/components/redbookfactory/linknotes/Linknotestable';

Vue.use(Router)

export default new Router({
  routes: [


    {
      path: '/',
      name: 'Formtable',
      component: Formtable,
    },
    {
      path: '/form',
      name: 'Form',
      component: Form,
    },
    {
      path: '/formtableall',
      name: 'Formtableall',
      component: Formtableall,
    },
    {
      path: '/formtablemore',
      name: 'Formtablemore',
      component: Formtablemore,
    },
    {
      path: '/formtableno',
      name: 'Formtableno',
      component: Formtableno,
    },
    {
      path: '/formtableyes',
      name: 'Formtableyes',
      component: Formtableyes,
    },
    {
      path: '/weiboguanggao',
      name: 'WeiboGuanggao',
      component: WeiboGuanggao,
    },
    {
      path: '/redbooktable',
      name: 'redbooktable',
      component: RedBooktable,
    },
    {
      path: '/redbookview',
      name: 'redbookview',
      component: RedBookview,
    },
    {
      path: '/redbookform',
      name: 'redbookform',
      component: RedBookform,
    },
    {
      path: '/uesrnoteskeyworld',
      name: 'uesrnoteskeyworld',
      component: UesrNoteskeyworld,
    }, {
      path: '/linknotesview',
      name: 'linknotesview',
      component: Linknotesview,
    },
    {
      path: '/linknotestable',
      name: 'linknotestable',
      component: Linknotestable,
    },

  ],

});



