    <Header></Header>
    <div class="demo-input-suffix" style="margin-top:80px;">
      <el-row>
        <el-col :span="12">
          <div class="grid-content bg-purple-light">
            <el-button type="text" class="margin5">{{name}}</el-button>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="grid-content bg-purple-light">
            <el-button
              type="warning"
              icon="el-icon-back"
              round
              @click="callback()"
              class="margin5"
            >返回</el-button>
            <el-button
              type="primary"
              icon="el-icon-refresh"
              circle
              @click="updataall"
              class="margin5"
            ></el-button>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-table
      :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)"
      border
      style="width: 100%;"
      height="65vh"
      v-loading="loading"
      :row-class-name="tableRowClassName"
    >
      <el-table-column label="广告条数" prop="userVO.totalAdverCount" align="center"></el-table-column>
      <el-table-column label="营收(万)" prop="userVO.totalRevenue" align="center" width="120px">
        <template slot-scope="scope">
          <p>{{scope.row.userVO.totalRevenue | totalRevenuefilters }}</p>
        </template>
      </el-table-column>
      <el-table-column label="昵称" prop="screenName" align="center"></el-table-column>
      <el-table-column label="内容类型" prop="crawlerType" align="center">
        <template slot-scope="scope">
          <p v-if="scope.row.crawlerType==1">全部内容</p>
          <p v-if="scope.row.crawlerType==2">广告内容</p>
          <p v-if="scope.row.crawlerType==3">非广告内容</p>
        </template>
      </el-table-column>
      <el-table-column label="uid" prop="wid" align="center"></el-table-column>
      <el-table-column label="时间范围" prop="userVO.totalDateStr" align="center" width="200px;"></el-table-column>
      <el-table-column label="直发内容(单条内容平均数据)" align="center">
        <el-table-column label="发布条数" prop="userVO.totalFrankCount" align="center"></el-table-column>
        <el-table-column label="转发" prop="userVO.totalFrankForward" align="center"></el-table-column>
        <el-table-column label="评论" prop="userVO.totalFrankComment" align="center"></el-table-column>
        <el-table-column label="赞" prop="userVO.totalFrankLike" align="center"></el-table-column>
        <el-table-column label="转赞评和" prop="userVO.totalFrankForwardCommentLike" align="center"></el-table-column>
      </el-table-column>
      <el-table-column label="转发内容(单条内容平均数据)" align="center">
        <el-table-column label="发布条数" prop="userVO.totalForwardCount" align="center"></el-table-column>
        <el-table-column label="转发" prop="userVO.totalForwardForward" align="center"></el-table-column>
        <el-table-column label="评论" prop="userVO.totalForwardComment" align="center"></el-table-column>
        <el-table-column label="赞" prop="userVO.totalForwardLike" align="center"></el-table-column>
